'use strict';
function init() {

  /* Init all components and functions */

  var colorSeries = ['deep-orange-600', 'orange-700', 'orange-500', 'orange-700'];
  var colors = colorSeries.map(function (e) {return 'mdl-color--' + e;});
  var textColors = colorSeries.map(function (e) {return 'mdl-color-text--' + e;});

  var errorContainer = document.querySelector('#errorContainer');
  var showMsg = function showMsg(message, actionHandler, actionText) {
    errorContainer.MaterialSnackbar.showSnackbar({
      message: message,
      timeout: 3000,
      actionHandler: actionHandler,
      actionText: actionText });

  };

  var idx = null,data = null;
  var mail = null;

  $.ajaxSetup({
    error: function error(xhr, status, exception) {
      mail = 'mailto:accel@pku.edu.cn?subject=NT18 OnAir Bug&body=status:' + status + '; exception:' + exception;
      setTimeout(function () {
        showMsg('Abstracts load error', function () {
          location.href = mail;
        }, 'Report');
      }, 3000);
    } });


  $.getJSON('data/abstracts.json', function (data_) {
    data = data_;
    idx = lunr(function () {
      this.ref('id');
      this.field('title');
      this.field('lab');
      this.field('content');
      this.field('author');
      data.forEach(function (doc) {
        this.add({
          id: doc.id,
          title: doc.title,
          author: doc.author.join(' '),
          lab: doc.lab.join(' '),
          content: doc.content,
          mapping: doc.mapping });

      }, this);
    });
  });


  // abstract tab processing

  $('#searchInput').keydown(function (e) {

    var $searchResult = $('#searchResult');
    var $searchInput = $('#searchInput');
    var $searchActions = $('#abstract').find('.mdl-card__actions');

    if (e.keyCode !== 13) return true;
    e.preventDefault();

    if (idx === null) {
      showMsg('Abstracts load error', function () {
        location.href = mail;
      }, 'Report');
      return;
    }

    $searchResult.html('');

    var result = idx.search($searchInput.val());
    if (result.length === 0) {
      showMsg('No result', function () {
        $searchInput.val('');
      }, 'Clear Input');
      if (!$searchActions.hasClass('hidden')) $searchActions.transition('fade out');
      return;
    }
    if ($searchActions.hasClass('hidden')) $searchActions.transition('fade in');

    result.forEach(function (elem, resultIndex) {

      var resultJson = data[elem.ref];
      var authors = resultJson.author.
      map(function (authorName, index) {return authorName + '<sup>' + resultJson.mapping[index] + '</sup>';}).join(', ');
      var labs = resultJson.lab.
      map(function (labName, index) {return '(' + (index + 1) + ') ' + labName;}).join(', ');
      var colorIndex = resultIndex % colors.length;

      $searchResult.append($('\n              <div class="mdl-card mdl-cell mdl-shadow--2dp\n                          mdl-cell--12-col-desktop mdl-cell--8-col-tablet mdl-cell--4-col-phone">\n                <div class="mdl-card__title mdl-color-text--white ' +


      colors[colorIndex] + '"\n                     style="min-height: 80px;">\n                  <h4 class="mdl-card__title-text" style="margin-top: 30px;">' +

      resultJson.title + '</h4>\n                </div>\n                <div class="mdl-card__supporting-text">\n                  <h6 style="margin: 12px;">' +


      authors + '</h6><br /><p>' + labs + '</p>\n                  <span class="resultInnerContent hidden"><hr />' +
      resultJson.content + '</span>\n                </div>\n                <div class="mdl-card__actions">\n                  <div class="mdl-button mdl-js-button mdl-js-ripple-effect ' +


      textColors[colorIndex] + ' btnToggle">\n                    Toggle Content\n                  </div>\n                </div>\n                <button class="mdl-button mdl-js-button mdl-js-ripple-effect mdl-button--icon ext-menu-button" \n                        id="favExt' +




      resultIndex + '"><i class="material-icons">more_vert</i>\n                </button>\n                <ul class="mdl-menu mdl-js-menu mdl-menu--bottom-right" data-mdl-for="favExt' +

      resultIndex + '">\n                  <li class="mdl-menu__item">Add to collections</li>\n                </ul>\n              </div>\n          ').



      click(function (e) {
        var $target = $(e.target.parentElement);
        if ($target.hasClass('btnToggle')) {
          $(e.currentTarget).find('.resultInnerContent').toggleClass('hidden');
        }
      }));

    });

    componentHandler.upgradeAllRegistered();

    $(".resultUnits").transition({ animation: 'slide down', duration: 200, interval: 100 });

  });

  // schedule tab

  $("#schedule").load('partial/schedule.html');

  componentHandler.upgradeAllRegistered();

}


function skipA2HS() {

  $('.mdl__loader.non-standalone').transition('fade out');

}
